/* overall layout */
html {
  height: 100%;
  width: 100%;
  font-family: Arial, Helvetica, sans-serif;
  color: rgba(255, 255, 255, 0.9);
  font-size: 16px !important;
}
body,
#root {
  margin: 0;
  padding: 0;
  height: 100%;
  width: 100%;
}

::selection {
  color: rgba(0, 0, 0, 0.9);
  background-color: rgba(255, 255, 255, 0.9);
}

p > a {
  color: rgba(255, 255, 255, 0.9);
  text-decoration: underline;
}

.hidden {
  display: none !important;
}

@media screen and (max-width: 480px) {
  .polaroid-logo {
    left: 0 !important;
    position: absolute !important;
  }
}

nav > a:hover {
  background: rgba(0, 0, 0, 0.1);
}

.frame {
  border-radius: 0.4em;
  background-color: whitesmoke;
  padding-top: 5.5%;
  height: 30.5rem;
  width: 25rem;
  color: rgba(0, 0, 0, 0.9);
  cursor: default;
  transition: all 0.3s ease-in-out;
}

.image {
  border: 0.1em solid black;
  width: 22.5rem;
  height: 22.5rem;
  margin: auto;
  background-size: cover;
  background-position: center top;
  position: relative;
  transition: all 0.3s ease;
}
.frame.active {
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}
.info {
  width: 100%;
  height: 100%;
  padding: 1em;
  display: grid;
  grid-template-rows: 1fr 1fr auto;
  grid-gap: 2em;
  gap: 2em;
  background-color: transparent;
  opacity: 0;
  transition: all 0.3s ease;
}
.info .links {
  display: flex;
  width: 100%;
  justify-content: space-around;
  justify-items: center;
  pointer-events: auto;
  position: relative;
}

.active .info {
  background-color: rgba(0, 0, 0, 0.8);
  opacity: 1;
  -webkit-backdrop-filter: blur(1px);
          backdrop-filter: blur(1px);
}

.label {
  width: 90%;
  height: 20%;
  margin: auto;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  justify-items: center;
  text-align: center;
  position: relative;
}
.label h3 {
  font-size: 24px;
  padding: 0.1rem;
  margin: 0.1rem;
  height: 25%;
}

.btn {
  text-transform: lowercase !important;
}

/* Devices without hover capability */
@media screen and (hover: none) {
  .frame {
    -webkit-user-select: none;
            user-select: none;
  }

  .info,
  .active .info {
    opacity: 1;
    background-color: rgba(0, 0, 0, 0.7) !important;
    -webkit-backdrop-filter: blur(2px);
            backdrop-filter: blur(2px);
  }
  .frame.active {
    -webkit-transform: none;
            transform: none;
  }
  .btn {
    font-size: 16px !important;
  }
}

/* Devices smaller than 480px wide */
@media screen and (max-width: 480px) {
  .frame {
    height: 25.5rem;
    width: 20rem;
  }
  .image {
    width: 18.5rem;
    height: 18.5rem;
  }
  .gallery {
    grid-template-columns: -webkit-max-content !important;
    grid-template-columns: max-content !important;
  }
  .fa-2x {
    -webkit-transform: scale(0.75);
            transform: scale(0.75);
  }
  .nav {
    grid-auto-flow: row;
  }
}

@media screen and (max-width: 480px) {
  #about-me {
    grid-template-rows: -webkit-max-content !important;
    grid-template-rows: max-content !important;
    grid-template-columns: auto !important;
  }
}

/* input color */
input,
textarea {
  color: rgba(255, 255, 255, 0.9);
}
/* label focus color */
input:focus + label,
textarea:focus + label {
  color: rgba(255, 255, 255, 0.9) !important;
}
/* label underline focus color */
input:focus,
textarea:focus,
.validate.valid {
  border-bottom: 1px solid rgba(255, 255, 255, 0.9) !important;
  box-shadow: 0 1px 0 0 #000 !important;
}

/* hide the ios-specific resume container by default */
#ios-resume-container {
  display: none;
}

/* this media query targets iOS devices only */
@supports (-webkit-touch-callout: none) {
  /* hide the normal resume container */
  #resume-container {
    display: none;
  }
  /* display the ios-specific resume container */
  #ios-resume-container {
    display: block;
  }
}

footer {
  display: grid;
  justify-items: stretch;
  align-items: stretch;
  grid-template-columns: repeat(5, 1fr);
  justify-content: space-evenly;
}
footer > a {
  text-align: center;
  color: rgba(255, 255, 255, 0.9);
  padding: 1em;
}

